import React from 'react'
import Blog from './Blog'

const Blogs = (blogs) => {


    const titleStyle = {
        paddingTop: 10,
        paddingLeft: 10,
        paddingBottom: 10,
      }


if (!blogs) {
    return null
}
    return (
<div>
<div>
      <h2 style={titleStyle}>blogs</h2>
   
   
      {blogs.blogs.map((blog) => (
          <Blog
            key={blog.id}
            blog={blog}
          />
        ))}
        </div>
</div>
    )
}

export default Blogs