import userService from '../services/users'
import { useState, useEffect } from 'react'
import styled from 'styled-components'

import Box from '@mui/material/Box';
import List from '@mui/material/List';
import ListItem from '@mui/material/ListItem';



const Button = styled.button`
background: Bisque;
font-size: 1em;
margin: 1em;
padding: 0.25em 1em;
border: 2px solid Chocolate;
border-radius: 3px;
`


const User = () => {

const [users, setUsers] = useState([])
     
const handleClick = (u) => {
    setUsers([u])
    }

const handleback = () => {
userService.getAll().then(users => {setUsers(users)})
    }

useEffect(() => {
    userService
      .getAll()
      .then(users => {
        setUsers(users)
      })
  }, [])

  
  const blogStyle = {
    paddingTop: 10,
    paddingLeft: 10,
    border: "solid",
    borderWidth: 1,
    marginBottom: 5,
    background: "white"
  };

  const postStyle = {
    paddingTop: 10,
    paddingLeft: 10,
    paddingRight: 10,
    paddingBottom: 10,
    border: "solid",
    borderWidth: 1,
    marginBottom: 5,
    background: "white"
  };


  const titleStyle = {
    paddingTop: 10,
    paddingLeft: 10
  }

  if (!users) {
    return null
  }

  if (users.length === 1) {
    return (
        <div>
            <h2 style={titleStyle}>{users.map(u => u.username)}'s entries</h2>
            {users.map(u => u.blogs.map(u => <List><ListItem><li key={u.id} style={postStyle}>{u.title}</li></ListItem></List>))}<Button onClick={() => handleback()}>Back</Button>
        </div>
    )
  }



    return (
        <div>
         
            <h2 style={titleStyle}>Users</h2>
            
           
            <Box sx={{ width: '100%', maxWidth: 360,  align: 'center'}}>
            <ul>
                {users.map(u => <List>
          <ListItem disablePadding><li key={u.id} style={blogStyle}> <strong>{u.username}:</strong> <strong>{u.blogs.length}</strong><Button onClick={() => handleClick(u)}>Show posts</Button></li></ListItem></List>)}
            </ul>
            </Box>
                    
        </div>
    )
}

export default User