import React, { useState } from "react";
import { useDispatch } from 'react-redux'
import { createBlog } from '../reducers/blogReducer'
import styled from 'styled-components'
import List from '@mui/material/List';
import ListItem from '@mui/material/ListItem';
import Notification from './Notification'


const Button = styled.button`
background: Bisque;
font-size: 1em;
margin: 1em;
padding: 0.25em 1em;
border: 2px solid Chocolate;
border-radius: 3px;
`

const Input = styled.input`
margin: 0.25em;
`

const BlogForm = ({ blogs, setBlogs, setErrorMessage, errorMessage }) => {
  const dispatch = useDispatch()

  const [newAuthor, setNewAuthor] = useState("");
  const [newTitle, setNewTitle] = useState("");
  const [newUrl, setNewUrl] = useState("");

  const handleAuthorChange = (event) => {
    setNewAuthor(event.target.value);
  };

  const handleTitleChange = (event) => {
    setNewTitle(event.target.value);
  };

  const handleUrlChange = (event) => {
    setNewUrl(event.target.value);
  };

  const titleStyle = {
    paddingTop: 10,
    paddingLeft: 10
  }


  const addBlogRedux = (event) => {
    event.preventDefault()
    const content = {
      title: event.target.title.value,
      author: event.target.author.value,
      url: event.target.author.value,
      likes: 0
    }

    dispatch(createBlog(content))
    setErrorMessage(`Blog Added!`);
    setTimeout(() => {
      setErrorMessage(null);
    }, 5000);

  }
  return (
    <div className="formDiv">
      <Notification message={errorMessage} />
      <h2 style={titleStyle}>Add a Blog</h2>
      <List>
      <form onSubmit={addBlogRedux}>
        <ListItem>
        <strong>Author:{" "}</strong>
        <Input
        name="author"
          id="author"
          value={newAuthor}
          onChange={handleAuthorChange}
          placeholder="write here author content"
        />
        </ListItem>
        
        <ListItem>
        <strong>Title:{" "}</strong>
        <Input
        name="title"
          id="title"
          value={newTitle}
          onChange={handleTitleChange}
          placeholder="write here title content"
        />
        </ListItem>
        
        <ListItem>
        <strong>Url:{" "}</strong>
        <Input
        name="url"
          id="url"
          value={newUrl}
          onChange={handleUrlChange}
          placeholder="write here url content"
        />
        
        </ListItem>
        <Button type="submit">save</Button>
      </form>
      </List>
    </div>
  );
};

export default BlogForm;
